* {
    text-align: center;
    font-family: "jua";
  }
  .homeimg {
    margin-top: 20px;
    width: 150px;
    height: auto;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .homeimg:hover {
    transform: rotate(10deg);
    box-shadow: black;
  }
  h1 {
    margin-top: 100px;
    margin-bottom: 100px;
    font-style: oblique;
    font-size: 70px;
  }
  span.complete {
    top: 20px;
    font-size: 80px;
    font-style: normal;
    color: mediumblue;
    animation: bounce 0.5s ease infinite alternate;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
      0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent,
      0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
  }
  span.computer {
    font-size: 80px;
    font-style: normal;
    color: dimgrey;
    animation: bounce 0.5s ease infinite alternate;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
      0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent,
      0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
  }
  @keyframes bounce {
    100% {
      top: -10px;
      text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
        0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc,
        0 50px 25px rgba(0, 0, 0, 0.2);
    }
  }
  h3 {
    margin-top: 50px;
    font-weight: bold;
    font-size: 50px;
    letter-spacing: 30px;
    cursor: grab;
    transition: letter-spacing 1s;
  }
  h3:hover {
    letter-spacing: 5px;
  }
  .des {
    margin-top: 50px;
    font-size: 20px;
    font-weight: 600;
    color: darkgrey;
  }
  